.modalOverlay {
  position: fixed;
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-secondary);
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}
.closeButton {
  border: none;
  cursor: pointer;
  background-color: transparent;
  margin-top: -366px;
  margin-right: -19px;
}
.addProductForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.addProductForm input {
  width: 100%;
  padding: 10px;
  border: none;
  border-bottom: 1px solid var(--color-muted);
  border-radius: 5px;
  font-size: 14px;
  color: var(--color-muted);
  outline: none;
  font-family: Verdana, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 17.01px;
  letter-spacing: 0.04em;
  text-align: left;
  transition: all 235ms ease-in-out;
}
.addProductBtn {
  box-shadow: 0px 4px 10px 0px var(--color-shadow);
  font-family: Verdana, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 17.01px;
  letter-spacing: 0.08em;
  text-align: center;
  background: var(--color-primary);
  color: var(--color-secondary);
  border: none;
  padding: 14px;
  height: 44px;
  width: 176px;
  border-radius: 30px;
  cursor: pointer;
  transition: all 235ms ease-in-out;
}
.addButton {
  box-shadow: 0px 4px 10px 0px var(--color-shadow);
  background: var(--color-primary);
  border: none;
  padding: 17px;
  height: 48px;
  width: 48px;
  border-radius: 30px;
  cursor: pointer;
  transition: all 235ms ease-in-out;
  display: flex;
  margin: 0 auto;
  margin-bottom: 20px;
  margin-top: 20px;
}
.addProductBtn:hover,
.addButton:hover {
  background: var(--color-primary);
  transform: translate3d(0, 2px, 0);
  box-shadow: 0px 4px 10px 0px var(--color-shadow);
}
.largerScreensForm {
  display: none;
}

@media screen and (min-width: 768px) {
  .mobileForm {
    display: none;
  }
  .largerScreensForm {
    display: block;
    margin-left: -9px;
  }
  .addProductForm {
    flex-direction: row;
    width: 70%;
  }
}
@media screen and (min-width: 1280px) {
  .addProductForm {
    width: 86%;
  }
}
